.fade-enter {
  opacity: 0.1;
  -webkit-transform: scale(0.6) translateX(-80vh);
      -ms-transform: scale(0.6) translateX(-80vh);
          transform: scale(0.6) translateX(-80vh);
}

.fade-enter-active {
  opacity: 1;
  -webkit-transform: scale(1) translateX(0%);
      -ms-transform: scale(1) translateX(0%);
          transform: scale(1) translateX(0%);
  -webkit-transition: all 500ms ease-out;
  -o-transition: all 500ms ease-out;
  transition: all 500ms ease-out;
}

.fade-exit {
  opacity: 1;
  -webkit-transform: scale(1) translateX(0%);
      -ms-transform: scale(1) translateX(0%);
          transform: scale(1) translateX(0%);
}

.fade-exit-active {
  opacity: 0.1;
  -webkit-transform: scale(0.6) translateY(-10vw);
      -ms-transform: scale(0.6) translateY(-10vw);
          transform: scale(0.6) translateY(-10vw);
  -webkit-transition: all 500ms ease-out;
  -o-transition: all 500ms ease-out;
  transition: all 500ms ease-out;
}



